.main {
 display: flex;
 flex-direction: row;
}

.createBox {
  display: flex;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: rgb(250, 250, 250);
  border-radius: 24px;
  border: 1px solid black;
  
}
.hidden {
  display: none;
}
.blur {
  opacity: 0.3;
}

.center {
  display: flex;
  
}

.homePage {
  width: 100%;
}

@media (max-width: 900px) { 
  .main { 
    flex-direction: column;
  }
}